@tailwind base;
@tailwind components;
@tailwind utilities;

/* ------------------------------
   ðŸŒˆ Styles gÃ©nÃ©raux du portfolio
--------------------------------*/
body {
  @apply bg-gradient-to-b from-blue-50 via-white to-blue-100 text-gray-900 transition-colors duration-700;
  margin: 0;
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto',
    'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-x: hidden;
}

/* ðŸŒ™ Mode sombre */
.dark body {
  @apply bg-gradient-to-b from-[#01161E] via-[#022C43] to-[#01161E] text-gray-100;
}

/* ðŸ’¡ Code / balises <code> */
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
  @apply bg-blue-100 text-blue-800 px-1 py-0.5 rounded dark:bg-[#022C43] dark:text-blue-300;
}

/* ðŸ”¹ Liens */
a {
  @apply text-blue-600 hover:text-blue-800 transition-colors duration-200 dark:text-blue-400 dark:hover:text-blue-300;
}

/* ðŸ”¹ Scrollbar stylÃ©e (Chrome, Edge, etc.) */
::-webkit-scrollbar {
  width: 10px;
  height: 10px;
}

::-webkit-scrollbar-track {
  background: #0090f1;
}

::-webkit-scrollbar-thumb {
  background: linear-gradient(180deg, #60a5fa, #2563eb);
  border-radius: 10px;
}

::-webkit-scrollbar-thumb:hover {
  background: linear-gradient(180deg, #3b82f6, #1e40af);
}

/* ðŸ”¹ SÃ©lection du texte */
::selection {
  background-color: #60a5fa;
  color: white;
}

/* ðŸ”¹ Animation dâ€™apparition globale (optionnelle) */
.fade-in {
  animation: fadeIn 0.8s ease-out forwards;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
